@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
*{box-sizing:border-box}body{margin:0;font-family:"Roboto", sans-serif;background-color:#f5f5f5}.generator{width:100%;padding:20px;border:1px solid #dbdbdb;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;margin:0px auto 0 auto;background-color:#f5f5f5;border-bottom:0}.generator__inputs{width:100%;margin-right:20px}.input-group{margin-bottom:15px}.input-group h4{color:#363636;font-size:1rem;margin:0 0 12px 0}.input-group__input{outline:none;color:#363636;width:100%;padding:10px;border-radius:5px;border:1px solid #b5b5b5;margin-bottom:15px}.generator__result-wrapper{max-width:500px;min-width:500px;min-height:500px;margin:-21px -21px -20px auto;border:1px solid #dbdbdb;border-bottom:0;background-color:#ededed;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;padding:20px}.generator__output{margin:0 auto 0 auto;width:100%;padding:20px;font-size:20px;font-weight:400;background-color:#4a4a4a;color:#fff}.btn-copy{border:0;margin:0 auto 0px auto;display:block;padding:20px;width:100%;font-size:2rem;cursor:pointer;background-color:#3abb67;color:#fff}.btn-copy:hover{background-color:#3ec46d}.transform-box{width:200px;height:200px;background-color:red;display:-webkit-inline-flex;display:inline-flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;color:#fff;font-size:2rem;font-weight:700}

.container {
    max-width: 1200px;
    margin: 0 auto;
}

.wrapper {
    background-color: #fff;
    padding: 20px;
    margin: 30px 0 40px;
    box-shadow: 0px 0px 35px 8px rgba(0, 0, 0, 0.05);
}

.page-container {
    position: relative;
    min-height: 100vh;
    padding-bottom: 111px;
    width: 100%;
}

.app {
    display: -webkit-flex;
    display: flex;
}

.header-app{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-between;justify-content:space-between;background-color:#179cde;padding:25px 15px 25px 15px}.header-title{font-size:1.6rem;color:#fff;margin:0}.header-navbar{margin:0;list-style:none;display:-webkit-flex;display:flex}.header-navbar li{margin:0 10px}

.footer{background-color:#20232a;padding:30px 20px;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;position:absolute;bottom:0;width:100%}.footer__list{list-style:none;padding:0;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;color:#fff;font-size:1rem}.footer__list li{padding:0 15px}.footer__list a{color:#fff;text-decoration:none;transition:opacity 0.3s ease}.footer__list a:hover{color:#fff;opacity:0.5}

.navigation__group{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;margin-bottom:30px}.navigation-btn{color:#fff;background-color:#179cde;border-radius:6px;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;height:100%;padding:20px;margin:10px;width:calc(33.3333333333% - 20px);text-decoration:none;font-weight:700;font-size:1.3rem;transition:background-color 0.3s ease}.navigation-btn:hover{background-color:rgba(23,156,222,0.8)}.navigation__label{margin:0 10px 5px 10px;font-weight:400;font-size:1.5rem}

.title {
    text-align: center;
    font-size: 4rem;
    margin-bottom: 20px;
    color: #179cde;
}

.subtitle {
    text-align: center;
    font-size: 1.5rem;
    margin-top: 0;
}

.border-radius__result {
    width: 200px;
    height: 200px;
    background-color: red;
    border: 0px solid #000000;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    /* transition: all 0.2s ease 0.02s; */
}

.rc-slider .rc-slider-rail{height:8px}.rc-slider .rc-slider-track{height:8px;background-color:#3abb67}.rc-slider .rc-slider-step{height:8px}.rc-slider .rc-slider-handle{height:24px;width:24px;margin-top:-7px;border-color:#3abb67;box-shadow:0 0 5px #3abb67}.rc-slider .rc-slider-handle:hover{border-color:#3ec46d}.rc-slider .rc-slider-handle:active{border-color:#3abb67;box-shadow:0 0 5px #3abb67}.rc-slider .rc-slider-handle:focus{border-color:#3abb67;box-shadow:0 0 5px #3abb67}.foxslider{margin-bottom:20px;padding:0 10px}.foxslider__text{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;margin-top:15px;padding:0 5px}.foxslider__text h5{font-size:1rem;font-weight:400;margin:0}

.box-shadow__result {
    width: 200px;
    height: 200px;
    background-color: red;
    /* transition: all 0.2s ease 0.02s; */
}




.nav-sidebar{max-width:300px;min-width:300px;border-right:1px solid #dbdbdb;background-color:#fff}.nav-sidebar__list{list-style:none;padding:0;margin:0 0 20px 0}.nav-sidebar__link{font-size:1.25rem;text-decoration:none;color:#000;font-weight:400;padding:10px;display:block;border-bottom:1px solid #dbdbdb;background-color:#ffffff;transition:all 0.3s ease}.nav-sidebar__link.active-link{background-color:#ededed}.nav-sidebar__subtitle{padding:0 10px;margin:0 0 12px 0;font-size:1.5rem;font-weight:700}





.background-gradient__result{width:100%;height:100%;background-color:#fff}

.background-image__result{width:100%;height:100%;background-color:#fff}

